@import url("https://fonts.googleapis.com/css?family=Rubik:300,400,500,700&display=swap");
#page-topbar {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 1002;
  background-color: var(--bs-header-bg);
  box-shadow: 0 0 24px 0 rgba(0, 0, 0, 0.06), 0 1px 0 0 rgba(0, 0, 0, 0.02);
}

.navbar-header {
  display: flex;
  -ms-flex-pack: justify;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  height: 70px;
  padding: 0 calc(24px / 2) 0 0;
}
.navbar-header .dropdown .show.header-item {
  background-color: #f8f9fa;
}

.navbar-brand-box {
  padding: 0 1.5rem;
  text-align: center;
  width: 240px;
}

.logo {
  line-height: 70px;
}
.logo .logo-sm {
  display: none;
}

.logo-light {
  display: none;
}

/* Search */
.search-wrap {
  background-color: var(--bs-input-bg);
  color: var(--bs-emphasis-color);
  z-index: 9997;
  position: absolute;
  top: 0;
  display: flex;
  width: calc(100% - 240px);
  right: 0;
  height: 70px;
  padding: 0 15px;
  transform: translate3d(0, -100%, 0);
  transition: 0.3s;
}
.search-wrap form {
  display: flex;
  width: 100%;
}
.search-wrap .search-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.search-wrap .search-input {
  flex: 1 1;
  border: none;
  outline: none;
  box-shadow: none;
  background-color: transparent;
}
.search-wrap .close-search {
  width: 36px;
  height: 64px;
  line-height: 64px;
  text-align: center;
  color: inherit;
  font-size: 24px;
}
.search-wrap .close-search:hover {
  color: #f1556c;
}

.search-wrap.open {
  transform: translate3d(0, 0, 0);
}

.megamenu-list li {
  position: relative;
  padding: 5px 0px;
}
.megamenu-list li a {
  color: var(--bs-body-color);
}

