// Font weight help class

.fw-medium {
  font-weight: $font-weight-medium;
}

.fw-semibold {
  font-weight: $font-weight-semibold;
}

// Social

.social-list-item {
  height: 2rem;
  width: 2rem;
  line-height: calc(2rem - 2px);
  display: block;
  border: 1px solid $gray-500;
  border-radius: 50%;
  color: $gray-500;
  text-align: center;
}
ol{
  list-style: none;
}

.w-xs {
  min-width: 80px;
}
.w-sm {
  min-width: 95px;
}
.w-md {
  min-width: 110px;
}
.w-lg {
  min-width: 140px;
}

.w-xl {
  min-width: 160px;
}

//Toast Components

.toast{
  &:not(:last-child) {
    margin-bottom: $toast-padding-x;
  }
}
.bg-light{
  background-color: var(--#{$prefix}border-color) !important;
}


.container-fluid {
  padding-left: 12px !important;
  padding-right: 12px !important;
}
